
import tool from "./leCompsTool.js";

let i18n = {
    comps:{
        "":{
            "zh":"",
            "en":"cancel"
        },
        "":{
            "zh":"",
            "en":"hint"
        },
        "":{
            "zh":"",
            "en":"OK"
        },
    }
}

let lang18n = (key)=>{
    let lang = tool.cookie.getCookie("lang");
    if(!lang){
        lang = "zh";
    }
    if(!i18n.comps[key]){
        return key;
    }
    return i18n.comps[key][lang];
}

export default lang18n;