let TREE_CONFIG = {
    /**
     * @description __parentId:-1__level:1
     * @param data:
     * @param currentNode:null
     * @param isRoot:
     */
    ASYNINITATTRIBUTE(data,currentNode,isRoot){
        if(data && data instanceof Array && data.length != 0){
            data.forEach(element=>{
                element.__tmpId = tool._idSeed.newId();
                element.__children = [];
                element.__cls = "fa-caret-right";
                element.__level = isRoot?1:currentNode.__level + 1;
                element.__expand = false;
                element.__parentNode = isRoot?null:currentNode;
                element.__parentId = isRoot?-1:currentNode.__tmpId;//__parentId:-1
                element.__color = "";
                element.__checkboxStatus = false;
            })
            return data;
        }
        return [];
    },
    /**
     * @description KEY
     * @param OPEN: children
     * @param UPDATECHILDREN: childrenajax
     * @param SELECTEDITEM: callback
     * @param CHECKBOX: checkbox
     */
    ACTIONKEY:{
        OPEN:"open",
        UPDATECHILDREN:"updateChilden",
        SELECTEDITEM:"selectedItem",
        CHECKBOX:"checkbox"
    }
}

